<template>
<main class="content container">
    <div class="loading" v-if="ordersLoading">
        <ProductLoader />
    </div>
    <div class="content__top">
        <ul class="breadcrumbs">
            <li class="breadcrumbs__item">
                <router-link class="breadcrumbs__link" :to="{name: 'main'}">
                    Каталог
                </router-link>
            </li>
            <li class="breadcrumbs__item">
                <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
                    Корзина
                </router-link>
            </li>
            <li class="breadcrumbs__item">
                <a class="breadcrumbs__link">
                    Оформление заказа
                </a>
            </li>
        </ul>

        <h1 class="content__title">
            Корзина
        </h1>
        <span class="content__info">
            {{ count }} товара
        </span>
    </div>

    <section class="cart">
        <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
            <div class="cart__field">
                <div class="cart__data">

                    <BaseFormText v-model="formData.name" :error="formError.name" title="ФИО" placeholder="Введите ваше полное имя" />

                    <BaseFormText v-model="formData.address" :error="formError.address" title="Адрес доставки" placeholder="Введите ваш адрес" />

                    <BaseFormText v-model="formData.phone" :error="formError.phone" title="Телефон" placeholder="Введите ваш телефон" />

                    <BaseFormText v-model="formData.email" :error="formError.email" title="Email" placeholder="Введите ваш Email" />

                    <BaseFormTextArea title="Комментарий к заказу" :error="formError.comment" v-model="formData.comment" placeholder="Ваши пожелания" />

                </div>

                <div class="cart__options">
                    <h3 class="cart__title">Доставка</h3>
                    <ul class="cart__options options">
                        <li class="options__item">
                            <label class="options__label">
                                <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                                <span class="options__value">
                                    Самовывоз <b>бесплатно</b>
                                </span>
                            </label>
                        </li>
                        <li class="options__item">
                            <label class="options__label">
                                <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                                <span class="options__value">
                                    Курьером <b>500 ₽</b>
                                </span>
                            </label>
                        </li>
                    </ul>

                    <h3 class="cart__title">Оплата</h3>
                    <ul class="cart__options options">
                        <li class="options__item">
                            <label class="options__label">
                                <input class="options__radio sr-only" type="radio" name="pay" value="card">
                                <span class="options__value">
                                    Картой при получении
                                </span>
                            </label>
                        </li>
                        <li class="options__item">
                            <label class="options__label">
                                <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                                <span class="options__value">
                                    Наличными при получении
                                </span>
                            </label>
                        </li>
                    </ul>
                </div>
            </div>

            <div class="cart__block">
                <ul class="cart__orders">
                    <li v-for="product in products" :key="product.productId" class="cart__order">
                        <h3>{{ product.product.title }}</h3>
                        <b>{{ product.product.price * product.amount | numberFormat }} ₽</b>
                        <span>Артикул: {{ product.product.id }}</span>
                    </li>
                </ul>

                <div class="cart__total">
                    <p>Доставка <b>{{ deliveryPrice | numberFormat }} ₽</b></p>
                    <p>Итого: <b>{{ count }}</b> товара на сумму <b>{{ totalPrice + deliveryPrice | numberFormat }} ₽</b></p>
                </div>

                <button class="cart__button button button--primery" type="submit">
                    Оформить заказ
                </button>
            </div>
            <div class="cart__error form__error-block" v-if="formErrorMessage">
                <h4>Заявка не отправлена!</h4>
                <p>
                    {{ formErrorMessage }}
                </p>
                <button v-if="ordersLoadingFailed" class="error-btn" @click.prevent="order">Попробовать еще раз</button>
            </div>
        </form>
    </section>
</main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import {
    mapState,
    mapGetters
} from 'vuex';
import ProductLoader from '@/components/ProductLoader.vue';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextArea from '@/components/BaseFormTextArea.vue';
import axios from 'axios';
import {
    API_BASE_URL
} from '@/config';

export default {
    name: 'OrderPage',
    components: {
        BaseFormText,
        BaseFormTextArea,
        ProductLoader,
    },
    data() {
        return {
            formData: {},
            formError: {},
            formErrorMessage: '',

            ordersLoading: false,
            ordersLoadingFailed: false,
        }
    },
    filters: {
        numberFormat
    },
    methods: {
        order() {
            this.formError = {}
            this.formErrorMessage = ''
            this.ordersLoading = true
            this.ordersLoadingFailed = false

            axios
                .post(API_BASE_URL + '/api/orders', {
                    ...this.formData
                }, {
                    params: {
                        userAccessKey: this.$store.state.userAccessKey
                    }
                })
                .then(response => {
                    this.$store.commit('resetCart')
                    this.$store.commit('updateOrderInfo', response.data)
                    this.$router.push({
                        name: 'orderInfo',
                        params: {
                            id: response.data.id
                        }
                    })
                })
                .catch(error => {
                    this.formError = error.response.data.error.request || {}
                    this.formErrorMessage = error.response.data.error.message
                    this.ordersLoadingFailed = true
                })
                .finally(() => {
                    this.ordersLoading = false
                })

        }
    },
    computed: {
        ...mapState({
            count: state => state.cardProducts.length,
            deliveryPrice: state => state.deliveryPrice
        }),
        ...mapGetters({
            products: 'cardDetailProducts',
            totalPrice: 'cartTotalPrice'
        }),
    },

}
</script>

<style lang="scss" scoped>
.container {
    position: relative;
}

.loading {
    width: 50%;
    height: 50%;
    overflow: auto;
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 5;
}
</style>
