<template>
<router-link class="header__cart" aria-label="Корзина с товарами" :to="{name: 'cart'}">
    <svg width="30" height="21" fill="currentColor">
        <use xlink:href="#icon-cart"></use>
    </svg>
    <span class="header__count" aria-label="Количество товаров">{{ count}}</span>
    <div class="product-msg" v-show="productAdded">Товар добавлен в корзину</div>
    <div class="product-msg" v-show="productAddSending">Добавляем товар в корзину</div>
</router-link>
</template>

<script>
import {
    mapState
} from 'vuex';
export default {
    name: 'CartIndicator',
    computed: {
        ...mapState({
            count: state => state.cardProducts.length,
            productAdded: state => state.productAdded,
            productAddSending: state => state.productAddSending,
        }),
    }
}
</script>

<style lang="scss" scoped>
.product-msg {
    font-size: 12px;
    white-space: nowrap;
}
</style>
