<template>
<main class="content container">
    <div class="content__top content__top--catalog">
        <h1 class="content__title">
            Каталог
        </h1>
        <span class="content__info">
            {{ countProducts }} товаров
        </span>
    </div>
    <div class="content__catalog">
        <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color.sync="filterColorValue" />
        <section class="catalog">
            <div v-if="productsLoading">
                <ProductLoader />
            </div>
            <div class="error-block" v-if="productsLoadingFailed">
                <div class="error-msg">Произошла ошибка</div>
                <button class="error-btn" @click.prevent="loadProducts">Попробовать еще раз</button>
            </div>
            <ProductList :products="products" />
            <div class="catalog__pagination pagination" v-show="!productsLoadingFailed && !productsLoading">
                <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
            </div>
        </section>
    </div>
</main>
</template>

<script>
import ProductList from '@/components/ProductList';
import BasePagination from '@/components/BasePagination';
import ProductFilter from '@/components/ProductFilter';
import ProductLoader from '@/components/ProductLoader';
import axios from 'axios';
import {
    API_BASE_URL
} from '../config'

export default {
    name: 'MainPage',
    components: {
        ProductList,
        BasePagination,
        ProductFilter,
        ProductLoader
    },
    data() {
        return {
            filterPriceFrom: 0,
            filterPriceTo: 0,
            filterCategoryId: 0,
            filterColorValue: 0,

            page: 1,
            productsPerPage: 3,

            productsData: null,

            productsLoading: false,
            productsLoadingFailed: false,

        }
    },
    computed: {

        products() {
            return this.productsData ? this.productsData.items.map(product => {
                return {
                    ...product,
                    image: product.image.file.url,
                    activeColor: product.colors[0].code
                }
            }) : [];
        },
        countProducts() {
            return this.productsData ? this.productsData.pagination.total : 0;
        },
    },
    methods: {
        loadProducts() {
            this.productsLoading = true
            this.productsLoadingFailed = false
            clearTimeout(this.loadProductsTimer);
            this.loadProductsTimer = setTimeout(() => {
                axios.get(API_BASE_URL + '/api/products', {
                        params: {
                            page: this.page,
                            limit: this.productsPerPage,
                            categoryId: this.filterCategoryId,
                            minPrice: this.filterPriceFrom,
                            maxPrice: this.filterPriceTo,
                            colorId: this.filterColorValue,
                        }
                    })
                    .then(response => this.productsData = response.data)
                    .catch(() => this.productsLoadingFailed = true)
                    .finally(() => this.productsLoading = false)
            }, 0);

        }
    },

    watch: {
        page() {
            this.loadProducts();
        },
        filterCategoryId() {
            this.loadProducts();
        },
        filterPriceFrom() {
            this.loadProducts();
        },
        filterPriceTo() {
            this.loadProducts();
        },
        filterColorValue() {
            this.loadProducts();
        },
    },

    created() {
        this.loadProducts()
    }
};
</script>

<style lang="scss" scoped>
.error-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.error-msg {
    margin-bottom: 25px;
}
</style>
