<template>
<main class="content container" v-if="productLoading">Загрузка товара...</main>
<main class="content container" v-else-if="!productData">Не удалось загрузить товар</main>
<main class="content container" v-else>
    <div class="content__top">
        <ul class="breadcrumbs">
            <li class="breadcrumbs__item">
                <router-link class="breadcrumbs__link" :to="{name: 'main'}">
                    Каталог
                </router-link>
            </li>
            <li class="breadcrumbs__item">
                <!-- 2 спопосб переключения страницы программно -->
                <a class="breadcrumbs__link" href="#" @click.prevent="$router.push({name: 'main'})">
                    {{category.title}}
                </a>
            </li>
            <li class="breadcrumbs__item">
                <a class="breadcrumbs__link">
                    {{product.title}}
                </a>
            </li>
        </ul>
    </div>

    <section class="item">
        <div class="item__pics pics">
            <div class="pics__wrapper">
                <img width="570" height="570" :src="product.image.file.url" :alt="product.title">
            </div>
        </div>

        <div class="item__info">
            <span class="item__code">Артикул: {{ product.id }}</span>
            <h2 class="item__title">
                {{ product.title }}
            </h2>
            <div class="item__form">
                <form class="form" action="#" method="POST" @submit.prevent="addToCart">
                    <b class="item__price">
                        {{ numberFormat(product.price) }} Р
                    </b>

                    <fieldset class="form__block">
                        <legend class="form__legend">Цвет:</legend>
                        <ul class="colors">
                            <li v-for="(color,idx) in product.colors" :key="idx" class="colors__item">
                                <label class="colors__label">
                                    <input class="colors__radio sr-only" type="radio" name="color-item" :value="color.title" :checked="color.code === colorProduct.color">
                                    <span class="colors__value" :style="{'background-color': color.code }">
                                    </span>
                                </label>
                            </li>
                        </ul>
                    </fieldset>

                    <div class="item__row">
                        <div class="form__counter">
                            <button @click="decrementQuantity" type="button" aria-label="Убрать один товар">
                                <svg width="12" height="12" fill="currentColor">
                                    <use xlink:href="#icon-minus"></use>
                                </svg>
                            </button>

                            <input type="text" v-model.number="amount" name="count">

                            <button @click="incrementQuantity" type="button" aria-label="Добавить один товар">
                                <svg width="12" height="12" fill="currentColor">
                                    <use xlink:href="#icon-plus"></use>
                                </svg>
                            </button>
                        </div>

                        <button class="button button--primery" type="submit" :disabled="productAddSending">
                            В корзину
                        </button>
                    </div>
                    <div v-show="productAdded">Товар добавлен в корзину</div>
                    <div v-show="productAddSending">Добавляем товар в корзину</div>
                </form>
            </div>
        </div>

        <div class="item__desc">
            <ul class="tabs">
                <li class="tabs__item">
                    <a class="tabs__link tabs__link--current">
                        Описание
                    </a>
                </li>
                <li class="tabs__item">
                    <a class="tabs__link" href="#">
                        Характеристики
                    </a>
                </li>
                <li class="tabs__item">
                    <a class="tabs__link" href="#">
                        Гарантия
                    </a>
                </li>
                <li class="tabs__item">
                    <a class="tabs__link" href="#">
                        Оплата и доставка
                    </a>
                </li>
            </ul>

            <div class="item__content">
                <p>
                    Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br>
                    Синхронизация со смартфоном<br>
                    Связь по Bluetooth Smart, ANT+ и Wi-Fi<br>
                    Поддержка сторонних приложений<br>
                </p>

                <a href="#">
                    Все характеристики
                </a>

                <h3>Что это?</h3>

                <p>
                    Wahoo ELEMNT BOLT GPS – это велокомпьютер, который позволяет оптимизировать свои велотренировки, сделав их максимально эффективными. Wahoo ELEMNT BOLT GPS синхронизируется с датчиками по ANT+, объединяя полученную с них информацию. Данные отображаются на дисплее, а также сохраняются на смартфоне. При этом на мобильное устройство можно установить как фирменное приложение, так и различные приложения сторонних разработчиков. Велокомпьютер точно отслеживает местоположение, принимая сигнал с целого комплекса спутников. Эта информация позволяет смотреть уже преодоленные маршруты и планировать новые велопрогулки.
                </p>

                <h3>Дизайн</h3>

                <p>
                    Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры устройства составляют всего 74,6 x 47,3 x 22,1 мм. что не превышает габариты смартфона. Корпус гаджета выполнен из черного пластика. На обращенной к пользователю стороне расположен дисплей диагональю 56 мм. На дисплей выводятся координаты и скорость, а также полученная со смартфона и синхронизированных датчиков информация: интенсивность, скорость вращения педалей, пульс и т.д. (датчики не входят в комплект поставки). Корпус велокомпьютера имеет степень защиты от влаги IPX7. Это означает, что устройство не боится пыли, а также выдерживает кратковременное (до 30 минут) погружение в воду на глубину не более 1 метра.
                </p>
            </div>
        </div>
    </section>
</main>
</template>

<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from "@/helpers/numberFormat";
import {
    incrementQuantity,
    decrementQuantity
} from "@/helpers/toIncremDecrem";
import axios from "axios";
import {
    API_BASE_URL
} from '../config';
import { mapActions, mapMutations, mapState } from 'vuex';

export default {
    name: 'ProductPage',
    data() {
        return {
            amount: 1,
            productData: null,
            productLoading: false,
            productLoadingFailed: false,
        }
    },
    methods: {
        ...mapActions(['addProductToCart']),
        ...mapMutations(['updateProductAdded', 'updateProductAddSending' ]),

        gotoPage,
        numberFormat,
        incrementQuantity,
        decrementQuantity,
        addToCart() {
            this.updateProductAdded(false);
            this.updateProductAddSending(true);
            this.addProductToCart({productId: this.product.id, amount: this.amount})
            .then(()=> {
                this.updateProductAdded(true);
                this.updateProductAddSending(false);
                setTimeout(()=> this.updateProductAdded(false), 1500)
            })
        },
        loadProduct() {
            this.productLoading = true
            this.productLoadingFailed = false

            axios.get(API_BASE_URL + '/api/products/' + this.$route.params.id)
                .then(response => this.productData = response.data)
                .catch(() => this.productLoadingFailed = true)
                .finally(() => this.productLoading = false)

        },
    },
    computed: {
        product() {
            return this.productData
        },
        category() {
            return this.productData.category
        },
        colorProduct() {
            return this.$store.getters.colorProduct(this.$route.params.id) ? this.$store.getters.colorProduct(this.$route.params.id) : ''
        },
        ...mapState({productAdded: state => state.productAdded}),
        ...mapState({productAddSending: state => state.productAddSending}), 
    },
    watch: {
        '$route.params.id': {
            handler() {
                this.loadProduct()
            },
            immediate: true //развнозначно вызову loadProducts в хуке created
        }
    },
}
</script>

<style lang="scss" scoped>

</style>
