<template>
<main class="content container">
    <div class="content__top">
        <ul class="breadcrumbs">
            <li class="breadcrumbs__item">
                <router-link class="breadcrumbs__link" :to="{name: 'main'}">
                    Каталог
                </router-link>
            </li>
            <li class="breadcrumbs__item">
                <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
                    Корзина
                </router-link>
            </li>
            <li class="breadcrumbs__item">
                <router-link class="breadcrumbs__link" :to="{name: 'order'}">
                    Оформление заказа
                </router-link>
            </li>
        </ul>

        <h1 class="content__title">
            Заказ оформлен <span>№ {{ numberOrder }}</span>
        </h1>
    </div>

    <section class="cart">
        <form class="cart__form form" action="#" method="POST">
            <div class="cart__field">
                <p class="cart__message">
                    Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
                    Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
                </p>

                <ul class="dictionary">
                    <li class="dictionary__item">
                        <span class="dictionary__key">
                            Получатель
                        </span>
                        <span class="dictionary__value">
                             {{ orderInfo.name }}
                        </span>
                    </li>
                    <li class="dictionary__item">
                        <span class="dictionary__key">
                            Адрес доставки
                        </span>
                        <span class="dictionary__value">
                            {{ orderInfo.address }}
                        </span>
                    </li>
                    <li class="dictionary__item">
                        <span class="dictionary__key">
                            Телефон
                        </span>
                        <span class="dictionary__value">
                            {{ orderInfo.phone }}
                        </span>
                    </li>
                    <li class="dictionary__item">
                        <span class="dictionary__key">
                            Email
                        </span>
                        <span class="dictionary__value">
                            {{ orderInfo.email }}
                        </span>
                    </li>
                    <li class="dictionary__item">
                        <span class="dictionary__key">
                            Способ оплаты
                        </span>
                        <span class="dictionary__value">
                            картой при получении
                        </span>
                    </li>
                </ul>
            </div>

            <div class="cart__block">
                <ul class="cart__orders">
                    <li v-for="product in basket" :key="product.product.id" class="cart__order">
                        <h3>{{ product.product.title }}</h3>
                        <b>{{ product.product.price * product.quantity | numberFormat }} ₽</b>
                        <span>Артикул: {{ product.product.id }}</span>
                    </li>
                </ul>

                <div class="cart__total">
                    <p>Доставка <b>{{ deliveryPrice | numberFormat }} ₽</b></p>
                    <p>Итого: <b>{{ basket.length }}</b> товара на сумму <b>{{ orderInfo.totalPrice + deliveryPrice | numberFormat }} ₽</b></p>
                </div>
            </div>
        </form>
    </section>
</main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import {
    mapState
} from 'vuex';
export default {
    name: 'OrderInfoPage',
    filters: {
        numberFormat
    },
    computed: {
        ...mapState({
            orderInfo: state => state.orderInfo ? state.orderInfo : [],
            basket: state => state.orderInfo ? state.orderInfo.basket.items : [],
            numberOrder: state => state.orderInfo ? state.orderInfo.basket.id : [],
            deliveryPrice: state => state.deliveryPrice,
        }),
    },
    created() {
        if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
            return
        }
        this.$store.dispatch('loadOrderInfo', this.$route.params.id )
    }

}
</script>

<style lang="scss" scoped>

</style>
