import Vue from "vue";
import Vuex from 'vuex';
import axios from 'axios';
import {
    API_BASE_URL
} from '../config';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        cardProducts: [],
        currentColor: [],
        userAccessKey: null,
        deliveryPrice: 500,

        cartProductsData: [],

        orderInfo: null,

        productAdded: false,
        productAddSending: false,
    },
    mutations: {
        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo;
        },
        resetCart(state) {
            state.cardProducts = [];
            state.cartProductsData = [];
        },
        updateCartProductAmount(state, { productId, amount }) {
            const item = state.cardProducts.find(item => item.productId === productId);
            if (item) {
                item.amount = amount
            }
        },
        changeColorCurrent(state, { productId, color }) {
            const item = state.currentColor.find(item => item.productId === productId);
            if (item) {
                item.color = color
            }
            else {
                state.currentColor.push({
                    productId,
                    color
                })
            }
        },
        updateUserAccessKey(state, accessKey) {
            state.userAccessKey = accessKey
        },
        updateCartProductsData(state, items) {
            state.cartProductsData = items
        },
        syncCardProducts(state) {
            state.cardProducts = state.cartProductsData.map(item => {
                return {
                    productId: item.product.id,
                    amount: item.quantity,
                }
            })
        },
        updateProductAdded(state, added) {
            state.productAdded = added
        },
        updateProductAddSending(state, addSending) {
            state.productAddSending = addSending
        }

    },
    getters: {
        cardDetailProducts(state) {
            return state.cardProducts.map(item => {
                const product = state.cartProductsData.find(p => p.product.id === item.productId).product;
                return {
                    ...item,
                    product: {
                        ...product,
                        image: product.image.file.url
                    }
                }
            })
        },
        cartTotalPrice(state, getters) {
            return getters.cardDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
        },
        colorProduct: (state) => (id) => {
            return state.currentColor.find(item => item.productId === id)
        }
    },
    actions: {
        loadOrderInfo(context, orderId) {
            console.log('lll')
            return axios.get(API_BASE_URL + '/api/orders/' + orderId, {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            })
                .then(response => {
                    console.log(response.data)
                    context.commit('updateOrderInfo', response.data)
                })
        },
        loadCart(context) {
            return axios.get(API_BASE_URL + '/api/baskets', {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            })
                .then(response => {
                    if (!context.state.userAccessKey) {
                        localStorage.setItem('userAccessKey', response.data.user.accessKey)
                        context.commit('updateUserAccessKey', response.data.user.accessKey);
                    }
                    context.commit('updateCartProductsData', response.data.items);
                    context.commit('syncCardProducts');
                })
        },
        addProductToCart(context, { productId, amount }) {
            return (new Promise(resolve => setTimeout(resolve, 1000)))
                .then(() => {
                    return axios.post(API_BASE_URL + '/api/baskets/products', {
                        productId: productId,
                        quantity: amount,
                    }, {
                        params: {
                            userAccessKey: context.state.userAccessKey
                        }
                    })
                        .then(response => {
                            context.commit('updateCartProductsData', response.data.items);
                            context.commit('syncCardProducts');
                        })
                })
        },
        updateCartProductAmount(context, { productId, amount }) {
            context.commit('updateCartProductAmount', { productId, amount })

            if (amount < 1) {
                return
            }

            return axios.put(API_BASE_URL + '/api/baskets/products', {
                productId: productId,
                quantity: amount,
            }, {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            })
                .then(response => {
                    context.commit('updateCartProductsData', response.data.items);

                })
                .catch(() => {
                    context.commit('syncCardProducts');
                })
        },
        deleteCartProduct(context, productId) {
            return axios.delete(API_BASE_URL + '/api/baskets/products', {
                params: {
                    userAccessKey: context.state.userAccessKey
                },
                data: {
                    productId,
                },
            })
                .then(response => {
                    context.commit('updateCartProductsData', response.data.items);
                    context.commit('syncCardProducts');
                })
        }
    }
});