<template>
<li class="catalog__item">
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}" >
        <img :src="product.image" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
        <a href="#">
            {{ product.title }}
        </a>
    </h3>

    <span class="catalog__price">
        {{ product.price | numberFormat}} Р
    </span>

    <ul class="colors colors--black">
        <li class="colors__item" v-for="(color, idx) in product.colors" :key="idx">
            <label class="colors__label" @click="switchColor(color.code)">
                <input class="colors__radio sr-only" type="radio" :value="color.code" v-model="defColor">
                <span class="colors__value" :style="{'background-color': color.code }">
                </span>
            </label>
        </li>
    </ul>
</li>
</template>

<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';

export default {
    name: 'ProductItem',
    filters: {
         numberFormat
    },
    data() {
        return {
            defColor: '',
        }
    },
    props: {
        product: Object,
    },
    methods: {
        switchColor(color) {
            sessionStorage.setItem(`changeColor${this.product.id}`, color)
        },
        gotoPage,
        addColors() {
            this.$store.commit('changeColorCurrent', {productId: this.product.id, color: this.defColor })
        }
    },
    watch: {
        defColor() {
            this.addColors()
        }
    },
    computed: {
        upDateModel() {
            if (sessionStorage.getItem(`changeColor${this.product.id}`)) {
                this.defColor = sessionStorage.getItem(`changeColor${this.product.id}`)
            }
            else {
                this.defColor = this.product.activeColor 
            }
        },
    },
    mounted() {
        this.upDateModel
        this.addColors
    }
};
</script>
