<template>
<ul class="catalog__pagination pagination">
    <li class="pagination__item">
        <button @click.prevent="paginate(page - 1)" :disabled="btnBackDisable" class="pagination__link pagination__link--arrow pagination__link--disabled" aria-label="Предыдущая страница">
            <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
            </svg>
        </button>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber" @click.prevent="paginate(pageNumber)">
        <a href="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" >
            {{ pageNumber }}
        </a>
    </li>
    <li class="pagination__item">
        <button @click.prevent="paginate(page + 1)" :disabled="btnNextDisable" class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница">
            <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
            </svg>
        </button>
    </li>
</ul>
</template>

<script>
export default {
    model: {
        prop: 'page',
        event: 'paginate'
    },
    name: 'BasePagination',
    data() {
        return {
            btnBackDisable: true,
            btnNextDisable: false,
        }
    },
    props: {
        page: Number, 
        count: Number, 
        perPage: Number,
    },
    methods: {
        paginate(page) {
            this.$emit('paginate', page)
            this.currentPage(page)
        },
        currentPage(activePage) {
            activePage === this.pages ? this.btnNextDisable = true : this.btnNextDisable = false
            activePage === 1 ? this.btnBackDisable = true : this.btnBackDisable = false
        }
    },
    computed: {
        pages() {
            return Math.ceil(this.count / this.perPage)
        },
     
    }
}
</script>

<style lang="scss" scoped>

</style>
